.App {
  height: 100vh;
  background-color: #0B2434;
  display: flex;
  justify-content: center;
  align-items: center;
}
.board{
  display: grid;
  grid-template: auto auto / repeat(5,1fr);
  align-content: center;
  gap: 30px;
  padding: 30px;
}
.dice{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 2px rgba(0,0,0,0.15);
  cursor: pointer;

}
.board button{
grid-column: 3;
padding: 10px;
border-radius: 8px;
background: #5035FF;
color: #fff;
border: none;
font-family: "Karla" sans-serif;
font-size: 16px;
font-weight: 400;
cursor: pointer;
}
.main{
  background: #F5F5F5;
  height: 80%;
  width: 50%;
  border-radius: 8px;
  padding: 30px;
  text-align: center;
}
.main p{
  color: #4A4E74;
}
